//Create and update a profile
export const createProfile = (formData, history, edit = false) => async dispatch => {
    try {
        const config = {
            headers: {
                "Content-Type": "application/json"
            }
        }
        const res = await axios.post("/api/profile", formData, config)
        dispatch({
            type: GET_PROFILE,
            payload: res.data
        })

        dispatch(setAlert(edit ? "Profile is updated" : "Profile is created", "success"))
        //history is the redirect to specific route after submitting the form, here if u create a profile and submit the form then go to dashboard
        //and to use it in the component we use withRouter
        if (!edit) {
            history.push("/dashboard")
        }
    } catch (err) {
        const errors = err.response.data.errors
        console.log(errors)
        if (errors) {
            errors.forEach(error => dispatch(setAlert(error.msg, "danger")));
        }
        dispatch({
            type: PROFILE_ERROR,
            payload: { msg: err.response.statusText, status: err.response.status }
        })
    }
}

//Add experience
