import axios from 'axios';
import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    USER_LOADED,
    AUTH_ERROR,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT,
    CLEAR_PROFILE
} from "./types";
import { setAlert } from "./alert";
import setAuthToken from "../utils/setAuthToken";

//load user
export const loadUser = () => async dispatch => {
    if (localStorage.token) {
        setAuthToken(localStorage.token)
    }
    try {
        const res = await axios.get("/api/auth")
        dispatch({
            type: USER_LOADED,
            payload: res.data//which here is the authenticated user
        })
    } catch (err) {
        dispatch({
            type: AUTH_ERROR
        })
    }
}

//register user
export const register = ({ name, email, password }) => async dispatch => {
    const config = {
        headers: {
            "Content-Type": "application/json"
        }
    }
    const body = JSON.stringify({ name, email, password })
    try {
        const res = await axios.post("/api/users", body, config)//res will contain data (in general data is the object of the response which we send in route in node) which here is the token and it contains other things such as config, headers,status,.....
        dispatch({
            type: REGISTER_SUCCESS,
            payload: res.data //res.data here is the object contains the token
        })
        // console.log(res)
        dispatch(loadUser())
    } catch (err) {
        const errors = err.response.data.errors
        console.log(errors)
        if (errors) {
            errors.forEach(error => dispatch(setAlert(error.msg, "danger")));
        }
        dispatch({
            type: REGISTER_FAIL
        })
    }
}

//login user
export const login = (email, password) => async dispatch => {
    const config = {
        headers: {
            "Content-Type": "application/json"
        }
    }
    const body = JSON.stringify({ email, password })
    try {
        const res = await axios.post("/api/auth", body, config)//res will contain data (in general data is the object of the response which we send in route in node) which here is the token and it contains other things such as config, headers,status,.....
        dispatch({
            type: LOGIN_SUCCESS,
            payload: res.data //res.data here is the object contains the token
        })
        dispatch(loadUser())
    } catch (err) {
        const errors = err.response.data.errors
        console.log(errors)
        if (errors) {
            errors.forEach(error => dispatch(setAlert(error.msg, "danger")));
        }
        dispatch({
            type: LOGIN_FAIL
        })
    }
}

//logout / clear profile
export const logout = () => dispatch => {
    dispatch({ type: CLEAR_PROFILE });
    dispatch({ type: LOGOUT });
}